
.nav{
    .close{
    opacity: 0;
  }
}
.list{
    @include flex;
    padding-left: 86px;
    padding-right: 84px;
    padding-top: 15px;
    justify-content: space-between;
    width: 1139px;
   
    &__item{
        
        a{
            font-family: 'Raleway';
font-style: normal;
font-weight: 500;
font-size: 15px;
line-height: 18px;
letter-spacing: 0.02em;
color: #1A2F3F;
        }
    }
    &__item.active{
        a{
              color: #337D5A;
        border-bottom: 3px solid #337D5A;
        padding-bottom: 11px;
        }
    }
  
    
}
.list__item:nth-child(4){
    a{
        letter-spacing: 0.1px;
        margin-left: 4px;
        margin-right: 4px;
    }
}
@media screen and (max-width:1220px) {
    .list {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        padding-left: 86px;
        padding-right: 84px;
        padding-top: 15px;
        -webkit-box-pack: justify;
        -ms-flex-pack: justify;
        justify-content: flex-start;
        width: 100%;
        flex-direction: column;
        align-items: center;
        padding-left: 0;
        padding-right: 0;
        padding-right: 0px;
    position: absolute;
    padding-left: 0px;
    height: 315px;
    margin-top: -447px;
    padding-top: 105px;
    z-index: 100;
    top: 0px;
    background: #337D5A;
    transition: all ease 1s;
        &__item{
            padding-bottom: 25px;
        }
        
    }
    .list__item a {
        font-family: "Raleway";
        font-style: normal;
        font-weight: 600;
        font-size: 26px;
        line-height: 18px;
        letter-spacing: -0.01em;
        color: white;
    }
    .nav{
        .close{
           opacity: 0;
            z-index: 100;
            position: absolute;
            width: 25px;
            right: 100px;
            top: 50px;
            transition: all ease 1s;
        
        }
    }
}
